import React, { useState } from "react";
import { withRouter } from 'react-router-dom';
import {
    FormWrapper,
    Input,
    StyledInlineErrorMessage,
    Select,
    Submit,
    CodeWrapper
} from './style'
import { Formik, Form, ErrorMessage } from "formik";
import * as Yup from 'yup';

const Signup = (props) => {
    const [formValues, setFormValues] = useState();
    let [submitted = false] = useState();
    return (
        <FormWrapper>
            <p className="content">Prepare for your career with a Project Management, Web-Development, Graphic design, or Digital
                        Marketing Internship at Northern.</p>
            <Formik
                initialValues={{
                    email: "",
                    department: ""
                }}
                validationSchema={Yup.object().shape({
                    email: Yup.string()
                        .email("The email is invalid.")
                        .required("Please enter your email."),
                    department: Yup.mixed().oneOf(
                        ['designer', 'development', 'product', 'other'],
                        'Invalid Department')
                        .required('Required'),
                })}
                onSubmit={(values, actions) => {
                    let mounted = true;
                    console.log(values);
                    setFormValues(values);
                    submitted = true;
                    const timeOut = setTimeout(() => {
                        if (mounted) {
                            actions.setSubmitting(false);
                            clearTimeout(timeOut);
                            props.history.push('success');
                        }
                    }, 2000);
                }}
            >
                {({
                    values,
                    errors,
                    touched,
                    handleSubmit,
                    isSubmitting,
                    validating,
                    valid
                }) => {
                    return (
                        <Form
                            className="input-form"
                            name="contact"
                            method="post"
                            onSubmit={handleSubmit}>
                            <div className="errorMsg">
                                <ErrorMessage name="email">
                                    {(msg) => (
                                        <StyledInlineErrorMessage>{msg}</StyledInlineErrorMessage>
                                    )}
                                </ErrorMessage>
                            </div>
                            <div className="inputWrapper">
                                <Input
                                    className="email"
                                    type="email"
                                    name="email"
                                    autoCapitalize="off"
                                    autoCorrect="off"
                                    autoComplete="email"
                                    placeholder="Your Email Address *"
                                    // Handle validation
                                    valid={touched.email && !errors.email ? "true" : "false"}
                                    error={touched.email && errors.email ? "true" : "false"}
                                />
                                <Select
                                    component="select"
                                    id="department"
                                    multiple={false}
                                    name="department"
                                    valid={touched.department && !errors.department ? "true" : "false"}
                                    error={touched.department && errors.department ? "true" : "false"}
                                >
                                    <option value="">Department</option>
                                    <option value="designer">Designer</option>
                                    <option value="development">Developer</option>
                                    <option value="product">Product Manager</option>
                                    <option value="other">Other</option>
                                </Select>
                            </div>


                            <Submit
                                type="submit"
                                disabled={!(touched.email && touched.department) || errors.email || errors.department || isSubmitting || submitted}>
                                {isSubmitting ? `Submiting...` : `Sign Up Now`}
                            </Submit>

                            <hr />
                            <CodeWrapper>
                                <strong>Errors:</strong> {JSON.stringify(errors, null, 2)}
                                <br />
                                <strong>Touched:</strong> {JSON.stringify(touched, null, 2)}
                                {formValues && <strong>Submitted values:</strong>}
                                {JSON.stringify(formValues, null, 2)}
                            </CodeWrapper>
                        </Form>
                    );
                }}
            </Formik>
        </FormWrapper>
    )
}

export default withRouter(Signup)