// form validation
export const Input = styled(Field)`
    margin: 0px 5px 36px 0;
    box-sizing: border-box;
    padding: 0.5em;
    height: 32px;
    width: 100%;
    color: #797979;
    background: #E5E5E5;
    border-radius: 1px;
    border: none;
    font-family: Merriweather;
    font-style: italic;
    &:focus,
    &:active {
        box-shadow: #D02035 0px 0px 2px 1px, #D02035 0px 0px 0px 1px;
        outline: none;
    }

   ${({ valid }) =>
        valid &&
        css`
        border: 1px solid light-blue;

        &:focus,
        &:active {
            box-shadow: #FFFFFF 0px 0px 2px 1px, #FFFFFF 0px 0px 0px 1px;
            outline: none;
        }
    `}

  ${({ error }) =>
        error &&
        css`
        border: 1px solid #D02035;
        outline: none;

        &:focus,
        &:active {
        box-shadow: #D02035 0px 0px 2px 1px, #D02035 0px 0px 0px 1px;
        outline: none;
        }
    `}
`;

